.game {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 10px;
  background: rgba(0, 0, 0, 0.55);
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
  text-align: center;
  transform: translate(-50%, -50%) scale(1.75);

  &.game-appear {
    opacity: 0;
    transition: opacity 400ms ease;
  }
  &.game-appear-active {
    opacity: 1;
  }
}